<template>
  <div class="wrapper wrapper-content">

    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>用户组管理</h5>
      </div>

      <div class="ibox-content">

        <div class="text-left" v-if="operation.add">
          <button class="btn btn-primary" @click="add()">新增</button>
        </div>

        <table class="table table-bordered text-center">
          <thead>
            <tr>
              <th>编号</th>
              <th>角色名称</th>
              <th>创建时间</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) of items" :key="item.id">
              <td>{{ index + 1 }}</td>
              <td>{{ item.name }}</td>
              <td>{{ item.created_at }}</td>
              <td>
                <template v-if="item.id != 2">
                  <button v-if="operation.edit" type="button" class="btn btn-sm btn-primary" @click="edit(item)">编辑</button>
                  <delete v-if="operation.del" @delConfirm="delConfirm" v-bind:item="item"></delete>
                </template>
              </td>
            </tr>
          </tbody>
        </table>

      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import Cache from '@/assets/cache'
import tabsService from '@/assets/template/tabs'
import Service from '@/assets/service/service'
export default {
  name: 'Role',
  data () {
    return {
      items: [],
      total: 0,
      page: 1,
      size: 10,
      condition: {},
      operation: {}
    }
  },
  methods: {
    getDataTables () {
      Service.Roles.get().then(response => {
        if (response.code == 200) {
          this.items = response.data
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/role/new', '用户组-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/role/' + item.id, '用户组-编辑')
    },
    delConfirm (item) {
      Service.Roles.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    }
  },
  created () {
    if (Cache.hasPermission('post_role')) {
      this.operation.add = true
    }
    if (Cache.hasPermission('update_role')) {
      this.operation.edit = true
    }
    if (Cache.hasPermission('delete_role')) {
      this.operation.del = true
    }
    this.getDataTables()
  },
  mounted () {
  }
}
</script>

<style scoped>
</style>



